<style>
.div-main-stats
{
  margin-left: 1rem !important;
}
.div-main
{
    background: #FAFEC5;
  background-image: linear-gradient(
    to bottom,
    rgb(194, 194, 194) 60%,
    white 50%,
    white 50%,
    rgb(255, 255, 255) 50%
  );
}
.bg-table-head
{
  background-color: rgb(219, 219, 219);
}
.live-text
{
  letter-spacing: 2px;
  animation: live 1s infinite;
}
