<script>
import Vue from 'vue'
import moment from 'moment'
import axios from 'axios'
export default({
  components:{
  },
  data () {
    return {
      stats: [],
      world: [],
      date: [],
      t_countries : []
    }
  },
  async mounted () {
    axios
      .get('https://corona.lmao.ninja/all')
      .then(response => (this.stats = response.data, this.date = response.data.updated))
    axios
      .get('https://corona.lmao.ninja/countries')
      .then(response => (this.world = response.data, this.t_countries = Object.keys(response.data).length))

  },
  methods:
  {
    moment
  }
})
</script>
